<template>
	<view>
		<!-- 地址 -->
		
		<navigator url="/pages/address/address?source=1" class="address-section">
			<view class="order-content" v-if="addressData.length == 0 ">
				<!-- <text class="iconfont" style="font-size: 38upx;">&#xe6b5;</text> -->
				<view class="cen" >
					<text class="address" style="color: #212121;margin-bottom: 20rpx;">请添加收获地址</text>
				</view>
				<text class="iconfont" style="font-size: 24upx;color:#818181;margin-right: 10upx;">&#xe660;</text>
			</view>
			<view class="order-content" v-else>
				<view class="cen" >
					<view class="top">
						<text v-if="addressData.status == 1" style="font-size: 14rpx;border:1px solid #E4BE90;color:#E4BE90;padding:0 10rpx;">默认</text>
						<text class="name">{{addressData.receiver}}</text>
						<text class="mobile">{{addressData.receive_tel}}</text>
					</view>
					<text class="address">{{addressData.province}}-{{addressData.city}}-{{addressData.district}}{{addressData.area}}</text>
				</view>
				<text class="iconfont" style="font-size: 24upx;color:#818181;margin-right: 10upx;">&#xe660;</text>
			</view>
			
			<image class="a-bg" src="data:image/png;base64,"></image>
		</navigator>
		
		<!-- 付款方式 -->
		<view class="itemList">
			<view class="title fz16" >
				选择付款方式
			</view>
			<view class="zhifu_zf" v-for="(payStypeVal, index) in payStype" :key="index" @click="zhifuChange(index)">
				<label for=""><image :src="payStypeVal.image" mode="" class="zhifu_image"></image></label>
				<view class="fkType" style="font-size: 32rpx;">{{ payStypeVal.name }}
					<view style="color:#666666;padding-top: 10upx;font-size: 28rpx;">{{ payStypeVal.cont }}</view>
				</view>
				<label><image :src="isCheckState==index?iconsel: iconNosel" class="section" :class="isCheckState==index?iconsel: iconNosel" ></image></label>                
				<view class="xian"></view>
			</view>
		</view>
		<!-- 订单信息 -->
		<view class="goods-section">
			<view class="b-b tiTop">订单信息</view>
			<view class="g-header b-b">
				<image class="logo" src="../../static/99.png"></image>
				<text class="name">由九九商城发出</text>
			</view>
			<!-- 商品列表 -->
			<view class="g-item">
				<image :src="src+ goodsIndex.imgurl"></image>
				<view class="right">
					<text class="title clamp">{{goodsDetail.name}}</text>
					<text class="spec">默认</text>
					<view class="price-box">
						<text class="price">￥{{goodsDetail.price}}</text>
						<uni-number-box :min="1" class="number" @change="bindChange"></uni-number-box>
					</view>
				</view>
			</view>
			<!-- 金额明细 -->
			<view class="yt-list">
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp">配送方式</text>
					<text class="cell-tip">到付</text>
				</view>
				<view class="yt-list-cell desc-cell b-b">
					<text class="cell-tit clamp">备注</text>
					<input class="desc" type="text" align="right" v-model="desc" placeholder="请填写备注信息" placeholder-class="placeholder" />
				</view>
				<view class="yt-list-cell b-b">
					<text class="cell-tit clamp"></text>
					<text class="cell-tip" style="">合计：<text class="primes">￥{{goodsDetail.price*number}}</text></text>
				</view>
			</view>
		</view>

		<!-- 底部 -->
		<view class="footer">
			<view class="price-content">
				<text>实付金额:</text>
				<text class="price-tip">￥</text>
				<text class="price">{{goodsDetail.price*number}}</text>
			</view>
			<text class="submit" @click="submit">提交订单</text>
		</view>

	</view>
</template>

<script>
	var request = require("@/common/service.js");
	import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
	var _self;
	export default {
		components: {uniNumberBox},
		data() {
			return {
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				iconsel: "/static/selected.png",
				iconNosel: "/static/static.png",
				 isCheckState: 0,
				 src:'https://fenxiao.99zhifukeji.com/',
				 goodsDetail:{},
				 goodsIndex:[],
				 imgurl:'',
				 number:1,
				 addressData:[],
				 payStype:[
				 { name:"微信支付", image:require('@/static/wximg.png'),cont:'快捷安全，支持微信支付',payType:'1'},
				 ],
				order_sn:'',
				fee:'',
				id:'',
			}
		},
		onLoad(options){
			_self = this;
			console.log(options)
			_self.id = options.id;
			_self.result = uni.getStorageSync('RESULT');
			request.setHToken(_self.result)
			_self.storeDetail()
			_self.getAddress()
		},
		onShow() {
			// _self.getAddress()
		},
		methods: {
			// 获取商品详情
			storeDetail:function(){
				let data={
					id:_self.id
				}
				request.storeDetail(data,function(res){
					 if(res.code==1){
						_self.goodsDetail=res.data;
						_self.goodsIndex=res.data.index[0];
					  }
				},function(err){
					 uni.hideLoading();
					 uni.showToast({
						 icon: 'none',
						 title: err.errmsg,
					 });
				})
			},
			// 获取地址
			getAddress:function(){
				request.getDefaultAddress(function(res){
					 if(res.code==1){
						_self.addressData=res.data;
					  }else{
						  uni.showToast({
							 icon: 'none',
							 title: res.msg,
						  });
					  }
				},function(err){
					 uni.hideLoading();
					 uni.showToast({
						 icon: 'none',
						 title: err.errmsg,
					 });
				})
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type){
				this.payType = type;
			},
			stopPrevent(){},
			// 支付方式选择
			zhifuChange(index){
				this.isCheckState = index;
			},
			//输入款数量
			bindChange:function(val){
				this.number=val;
			},
			submit:function(){
				let data={
					goodsId:_self.id,
					receiver:_self.addressData.receiver,
					phone:_self.addressData.receive_tel,
					goods_num:_self.number,
					address:_self.addressData.province+_self.addressData.city+_self.addressData.district,
					unit_price:_self.goodsDetail.marketPrice,
					total:_self.goodsDetail.marketPrice*_self.number,
					pay_type:_self.isCheckState,
					remark:_self.desc
				}
				console.log(data)
				request.storeSetOrder(data,function(res){
					 if(res.code==1){
						_self.order_sn=res.data.order_sn;
						console.log(_self.order_sn)
						console.log(_self.isCheckState)
						// app
						 // #ifdef APP-PLUS
						 if(_self.isCheckState==0){
						 	uni.request({
						 		url: 'https://fenxiao.99zhifukeji.com/api/store/apppay',
						 		data: {
						 			order_sn:_self.order_sn
						 		},
						 		header: {//自定义请求头信息
						 			'custom-header': 'application/json',
						 			 'token':_self.result
						 		},
						 		success: (res) => {
						 			console.log(res)
						 			//调起支付
						 			uni.requestPayment({
						 				provider: 'wxpay',
						 				timeStamp: res.data.data.timeStamp,
						 				nonceStr: res.data.data.nonceStr,
						 				package: res.data.data.package,
						 				signType: 'MD5',
						 				paySign: res.data.data.sign,
						 				success: function(res) {
						 					console.log(res)
						 					console.log('success:' + JSON.stringify(res));
						 				},
						 				fail: function(err) {
						 					console.log('fail:' + JSON.stringify(err));
						 				}
						 			});
						 		}
						 	});
						 }else{
						 	
						 }
						// #endif
						// 微信小程序
						// #ifdef MP-WEIXIN
							if(_self.isCheckState==0){
								uni.login({
									provider: 'weixin',
									success: function(loginRes) {
										let code = loginRes.code;
										console.log('code',code)
										console.log('order_sn',_self.order_sn)
										// 请求后台数据 prepay_id paySign nonceStr timeStamp
										uni.request({
											url: 'https://fenxiao.99zhifukeji.com/api/store/pay',
											data: {
												code: code,
												order_sn:_self.order_sn
											},
											header: {//自定义请求头信息
												'custom-header': 'application/json',
												 'token':_self.result
											},
											success: (res) => {
												console.log(res)
												//调起支付
												uni.requestPayment({
													provider: 'wxpay',
													timeStamp: res.data.data.timeStamp,
													nonceStr: res.data.data.nonceStr,
													package: res.data.data.package,
													signType: 'MD5',
													paySign: res.data.data.sign,
													success: function(res) {
														console.log(res)
														uni.showToast({
															 icon: 'none',
															 title: res.msg,
														 });
														 setTimeout(function(){ 
															 uni.redirectTo({
																url: '/pages/order/paySuc'
															 })
														}, 1000);
														console.log('success:' + JSON.stringify(res));
													},
													fail: function(err) {
														console.log('fail:' + JSON.stringify(err));
													}
												});
											}
										});
									}
								});
							}else{
								
							}
						// #endif
					  }
				},function(err){
					 uni.hideLoading();
					 uni.showToast({
						 icon: 'none',
						 title: err.errmsg,
					 });
				})
			},
		}
	}
</script>

<style lang="scss">
	page {
		background: #F3F3F3;
		padding-bottom: 100upx;
	}

	.address-section {
		margin:25upx;
		background: #fff;
		position: relative;
		border-radius: 20upx;
		.order-content {
			display: flex;
			align-items: center;
			padding: 20upx;
			border-radius: 20upx;
			box-shadow: 5px 5px 5px rgba(0,0,0,.1);
		}
		.cen {
			display: flex;
			flex-direction: column;
			flex: 1;
			font-size: 28upx;
			color: $font-color-dark;
		}

		.name {
			font-size: 28upx;
			margin :0 24upx;
		}

		.address {
			margin-top: 16upx;
			margin-left: 8upx;
			color: $font-color-light;
		}

		.a-bg {
			position: absolute;
			left: 0;
			bottom: 0px;
			display: block;
			width: 100%;
			height: 5upx;
			padding:0 8px;
		}
	}

	.goods-section {
		margin: 25upx;
		background: #fff;
		padding-bottom: 1px;
		border-radius: 20upx;
		box-shadow: 3px 3px 10px rgba(0,0,0,.1);
		.tiTop{
			font-size: 32upx;
			color:#616161;
			margin-left: 25upx;
			padding-top: 20upx;
			}
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}

		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}

		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}

		.g-item {
			display: flex;
			margin: 20upx 30upx;

			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}

			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}

			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}

			.spec {
				font-size: 26upx;
				color:$font-color-light;
			}

			.price-box {
				display: flex;
				align-items: center;
				justify-content: space-between;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
				.price {
					margin-bottom: 4upx;
					flex:4;
				}
				.number{
					flex:1;
					// margin-left: 30%;
				}
			}

			.step-box {
				position: relative;
			}
			
		}
		.yt-list {
			margin-top: 16upx;
			background: #fff;
		}
		
		.yt-list-cell {
			display: flex;
			align-items: center;
			padding: 10upx 30upx 10upx 40upx;
			line-height: 70upx;
			position: relative;
		
			&.cell-hover {
				background: #fafafa;
			}
		
			&.b-b:after {
				left: 0upx;
			}
		
			.cell-icon {
				height: 32upx;
				width: 32upx;
				font-size: 22upx;
				color: #fff;
				text-align: center;
				line-height: 32upx;
				background: #f85e52;
				border-radius: 4upx;
				margin-right: 12upx;
		
				&.hb {
					background: #ffaa0e;
				}
		
				&.lpk {
					background: #3ab54a;
				}
		
			}
		
			.cell-more {
				align-self: center;
				font-size: 24upx;
				color: $font-color-dark;
				margin-left: 8upx;
				margin-right: -10upx;
			}
		
			.cell-tit {
				flex: 1;
				font-size: 26upx;
				color: $font-color-dark;
				margin-right: 10upx;
			}
		
			.cell-tip {
				font-size: 26upx;
				color: $font-color-dark;
		
				&.disabled {
					color: $font-color-light;
				}
		
				&.active {
					color: #E4BE90;
				}
				&.red{
					color:#E4BE90;
				}
			}
		
			&.desc-cell {
				.cell-tit {
					max-width: 90upx;
				}
			}
		
			.desc {
				flex: 1;
				font-size: $font-base;
				color: $font-color-dark;
			}
			.primes{
				color: $uni-color-primary;
				font-weight: 700;
			}
		}
	}
	
	
	/* 支付列表 */
	.itemList{
		background-color: #fff;
		padding: 40upx;
		padding-top:30upx;
		margin: 25upx;
		border-radius: 20upx;
		position:relative;
		box-shadow: 3px 3px 10px rgba(0,0,0,.1);
		.zhifu_zf{
			display: flex;
			.zhifu_image{
				flex:1;
				width:84upx;
				height:84upx;
				margin-left: 10upx;
				margin-top: 30upx;
			}
			.fkType{
				flex:1;
				margin-left: 50upx;
				margin-top: 30upx;
			}
			.section{
				flex:1;
				width:39upx;
				height:39upx;
				margin-top:60upx;
			}
		}
	}
	
	.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: flex-end;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: #000;
		box-shadow: 0 -1px 5px rgba(0,0,0,.1);
		.price-content{
			padding-right: 30upx;
		}
		.price-tip{
			color: #E4BE90;
			margin-left: 8upx;
		}
		.price{
			font-size: 36upx;
			color: #E4BE90;
		}
		.submit{
			display:flex;
			align-items:center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #000;
			font-size: 32upx;
			background-color: #E4BE90;
		}
	}

</style>
